<template>
	<view>
		<view class="uni-tabbar">
		<!-- 遍历tabbar -->
			<view class="uni-tabbar__item" v-for="(item,index) in tabbar" :key="index" @tap="changeTab(item)">
				<view class="uni-tabbar__bd" v-if="index!==1">
					<view class="uni-tabbar__icon" >
					<!-- 判断tabbar点击过后的图片路径 -->
						<image v-if="item.pagePath == pagePath" class="icon-img" :src="item.selectedIconPath" mode="aspectFit"></image>
						<image v-else class="icon-img" mode="aspectFit" :src="item.iconPath"></image>
					</view>
				</view>
        <view class="publish-circle" v-else>
          <view class="inner-circle">
             <image src="/static/tab-icons/发布.png" class="img"></image>
          </view>
        </view>
				<!-- 判断tabbar点击前后的样式 -->
				<view class="uni-tabbar__label" :class="{'active': item.pagePath == pagePath}">
					{{item.text}}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
		//从父级继承过来的属性 需要在父级中使用:pagePath='pagePath',
			pagePath:{
        type:String,
        default:'pages/home/home'
      }
		},
		data() {
			return {
				// page: '',
				showPage: false,
				containerHeight: 400,
				//公共的tabbar
				tabbar: [
          {
            "text": "首页",
            "pagePath": "pages/home/home",
            "iconPath": "static/tab-icons/店铺icon.png",
            "selectedIconPath": "static/tab-icons/店铺icon.png"
          },
          {
            "text": "+",
            "pagePath": "pages/publish/publish",
            "iconPath": "static/tab-icons/发布.png",
            "selectedIconPath": "static/tab-icons/发布.png"
          },
          {
            "text": "个人中心",
            "pagePath": "pages/ my/ my",
            "iconPath": "static/tab-icons/my.png",
            "selectedIconPath": "static/tab-icons/my.png"
          }
				]
			};
		},
		methods: {
			changeTab(item) {
				this.page = item.pagePath 
         uni.reLaunch({
              url: '/' + this.page
         })
			},
		}
	}
</script>

<style lang="scss">
	.uni-tabbar {
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 999;
		width: 100%;
		display: flex;
		justify-content: space-around;
		height: 100rpx;
		padding: 16rpx 0;
		box-sizing: border-box;
		border-top: solid 1rpx #ccc;
		background-color: #fff;
		box-shadow: 0px 0px 17rpx 1rpx rgba(206, 206, 206, 0.32);

		.uni-tabbar__item {
			display: block;
			line-height: 24rpx;
			font-size: 20rpx;
			text-align: center;
			width: 25%;
		}

		.uni-tabbar__icon {
			height: 24px;
			line-height: 24px;
			text-align: center;
		}

		.icon {
			display: inline-block;
		}

		.uni-tabbar__label {
			line-height: 24rpx;
			font-size: 24rpx;
			color: #999;

			&.active {
				color: #C81816;
			}
		}

		.icon-img {
			height: 24px;
			width: 24px;
		}
	}
  
  .publish-circle{
    width: 130rpx;
    height: 130rpx;
    background-color: #fff;
    box-shadow: 0 0 3px 0 #ccc;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    .inner-circle{
      width: 100rpx;
      height: 100rpx;
      background-color: greenyellow;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      .img{
        width: 70%;
        height: 70%;
      }
    }
  }
</style>

